<template>
  <div class="root">
  <div v-for="item in displayText">
    <DisplayQA :Question=item.Question :Answer=item.Answer />
  </div>
</div>
</template>

<script>
import DisplayQA from "../../components/DisplayQA";
export default {
  name: "InternShip",
  data(){
    return {
      displayText: [
        {
          Question: 'Q: 如果拿到了offer，必须提前进行实习吗？',
          //此处与原稿不同的是"地"
          Answer:   'A: 我们建议拿到offer的同学们都能来公司进行实习，校招生可以通过实习更直观地了解到博乐科技，同时，实习期可抵消试用期。(考虑到应届生会涉及毕业设计，实习期可在双方协商后确认)。'
        },
        {
          Question: 'Q: 如果没办法实习，什么时候报到？',
          Answer:   'A: 校招HR将会与毕业生协商具体报到时间。'
        }
      ]
    }
  },
  components:{
    DisplayQA
  }
}
</script>

<style scoped>

</style>



